.app-wrapper {
    display: grid;
    width: 1200px;
    margin: 0 auto;

    grid-template-areas:
  "header header"
  "nav content";
    grid-template-rows: 70px 1fr;
    grid-template-columns: 2fr 10fr;
    /* grid-gap: 10px; */
}

.app-wrapper-content {
    grid-area: content;
    background-color: #1e2d56;
    color: white;
    padding: 10px;
    min-height: 650px;
}